<template>
  <v-app>
    <v-app-bar
    app
    absolute
    color="black"
    dark
    >
    <v-icon>mdi-settings</v-icon>
      <v-btn text>
      {{ title }} Settings Panel
      </v-btn>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-content>
      <v-tabs
        vertical
        v-if="formSections"
      >
        <v-tab
          v-for="(section,index) in formSections"
          :key="index"
          style="justify-content:left;"
        >
        <v-icon left>{{section.section_icon}}</v-icon>
        {{section.section_title}}
        </v-tab>
        <v-tab-item
          v-for="(section,index) in formSections"
          :key="index"
        >
          <v-card flat>
            <v-card-text class="pa-0">
              <v-list two-line>
                <v-list-item-group v-model="item" color="primary">
                  <template v-for="field in section.form_fields">
                    <v-list-item :key="field.option_slug">
                      <v-list-item-avatar>
                        <v-btn
                          icon
                          @click="fieldSetup(field)"
                        >
                          <v-icon>mdi-circle-edit-outline</v-icon>
                        </v-btn>
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title>{{field.option_name}}</v-list-item-title>
                        <v-list-item-subtitle>{{model[field.option_slug]}}</v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-list-item-group>
              </v-list>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs>
      <v-dialog v-if="setupContext" v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">{{setupContext.option_name}}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <component v-model="model[setupContext.option_slug]" :is="setupContext.component" v-bind="setupContext.props"/>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" text :disabled="isLoading" @click="editClose">Close</v-btn>
            <v-btn color="primary" text :loading="isLoading" @click="editSave">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-content>
  </v-app>
</template>

<script>
import {VTextField} from 'vuetify/lib';
import {VSelect} from 'vuetify/lib';
import {VCombobox} from 'vuetify/lib';
import {VRadioGroup} from 'vuetify/lib';
import {VRadio} from 'vuetify/lib';
import {VCheckbox} from 'vuetify/lib';
import {VSwitch} from 'vuetify/lib';
import {VDatePicker} from 'vuetify/lib';
import {VColorPicker} from 'vuetify/lib';
/*global parent*/
export default {
  name: 'App',
  components: {
    VTextField,VSelect,VCombobox,VCheckbox,VRadioGroup,VRadio,VDatePicker,VColorPicker,VSwitch
  },
  data: () => ({
    formSections:null,
    model:null,
    title:null,
    setupContext:null,
    dialog:false,
    item:null,
    ajaxURL:null,
    prevValue:null,
    isLoading:false
  }),
  methods:{
    fieldSetup(field){
      this.prevValue = this.model[field.option_slug];
      this.setupContext = field;
      this.dialog = true;
    },
    editClose(){
      this.model[this.setupContext.option_slug] = this.prevValue;
      this.dialog = false;
    },
    editSave(){
      this.isLoading = true;
      fetch(this.ajaxURL + '?action=vao_update&settings='+ this.settingsSlug + '&key=' + this.setupContext.option_slug + '&value=' + this.model[this.setupContext.option_slug])
      .then(response=>{
        return response.text();
      })
      .then(text=>{
        this.isLoading = false;
        if (text == 'OK'){
          this.dialog = false;
        }else{
          alert('Error - Could not save')
          this.editClose();
        }
      })
      .catch(()=>{
        this.isLoading = false;
        alert('Error - Could not save')
        this.editClose();
      })
    }
  },
  mounted(){
    let temp = {};
    parent.FormConfiguration.form_sections.forEach(section=>{
      section.form_fields.forEach(field=>{
        temp[field.option_slug] = parent.CurrentSettings[field.option_slug] || field['default_value'];
      })
    })
    this.model = temp;
    this.settingsSlug = parent.FormConfiguration.settings_slug;
    this.formSections = parent.FormConfiguration.form_sections;
    this.title = parent.FormConfiguration.page_title;
    this.ajaxURL = parent.AjaxURL;
  }
};
</script>
